import client from "../_api";
import accountsApi from "../_api/accounts.api";

const addCompanyService = {
  checkIIN(iin) {
    return accountsApi.checkIIN(iin);
  },
  sendFiles(data, handleUploadProgress = () => {}) {
    return client.post('ocr/identity', data, {onUploadProgress: handleUploadProgress});
  },
  saveFiles(data) {
    return client.post('filenet/upload', data);
  }
};

export default addCompanyService;
