import client from "./index";

const authApi = {
  sendPassword(phone) {
    return client.post('/sms/'+phone);
  },
  checkPassword(msisdn, code) {
    return client.post('/auth/signin', {msisdn, code}).then(res => {
      res.data = {
        token_type: 'Bearer',
        access_token: res.data.token,
      };
      return res;
    });
  },
};

export default authApi;
