import axios from 'axios';

const user = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : false;
const token = user ? user.token_type + ' ' + user.access_token : '';
// const lastActivity = localStorage.getItem('last-activity') ? new Date(localStorage.getItem('last-activity')) : new Date(0);
const baseURLS = {
  'development': 'http://localhost:8080/api',
  'production': '/api',
};

console.log(token);

const headers = {
  'Content-Type': 'application/json',
};

if (token !== '') {
  headers['Authorization']= token
}

const client =  axios.create({
  baseURL: baseURLS[process.env.NODE_ENV] || 'http://localhost:8080/api',
	headers
});

